"use client";

import type { Question, Trait } from "@/lib/types";
import { Button } from "./ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "./ui/card";
import { Progress } from "./ui/progress";

interface QuizQuestionsProps {
  question: Question;
  onAnswer: (questionId: number, trait: Trait) => void;
  progress: number;
}

export default function QuizQuestions({ question, onAnswer, progress }: QuizQuestionsProps) {
  const Icon = question.icon;

  return (
    <div key={question.id}>
      <Progress value={progress} className="mb-4" />
      <Card className="shadow-lg animate-in fade-in-0 zoom-in-95 duration-500">
        <CardHeader className="text-center">
            <div className="mx-auto bg-secondary p-3 rounded-full w-fit mb-2">
                <Icon className="w-8 h-8 text-primary" />
            </div>
          <CardTitle className="text-2xl font-headline">{question.text}</CardTitle>
          <CardDescription>Question {question.id} of 5</CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mt-4">
            {question.options.map((option, index) => (
              <Button
                key={index}
                variant="outline"
                size="lg"
                className="h-auto py-4 whitespace-normal justify-start text-left hover:bg-accent"
                onClick={() => onAnswer(question.id, option.trait)}
              >
                {option.text}
              </Button>
            ))}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
