"use client";

import { useState } from "react";
import type { Recommendation, Trait } from "@/lib/types";
import { quizQuestions } from "@/lib/data";
import QuizQuestions from "@/components/QuizQuestions";
import QuizResults from "@/components/QuizResults";
import { getCoachingRecommendations } from "@/lib/actions";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Loader2, Sparkles } from "lucide-react";
import Header from "@/components/Header";

type QuizState = "idle" | "in_progress" | "loading" | "results";

export default function QuizPage() {
  const [quizState, setQuizState] = useState<QuizState>("idle");
  const [answers, setAnswers] = useState<Record<number, Trait>>({});
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [recommendations, setRecommendations] = useState<Awaited<ReturnType<typeof getCoachingRecommendations>> | null>(null);

  const handleStart = () => {
    setQuizState("in_progress");
  };

  const handleAnswer = (questionId: number, trait: Trait) => {
    const newAnswers = { ...answers, [questionId]: trait };
    setAnswers(newAnswers);

    if (currentQuestion < quizQuestions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      handleFinish(newAnswers);
    }
  };
  
  const handleFinish = async (finalAnswers: Record<number, Trait>) => {
    setQuizState("loading");
    const result = await getCoachingRecommendations(finalAnswers);
    setRecommendations(result);
    setQuizState("results");
  };
  
  const handleRestart = () => {
    setAnswers({});
    setCurrentQuestion(0);
    setRecommendations(null);
    setQuizState("idle");
  };

  const progress = (currentQuestion / quizQuestions.length) * 100;

  return (
    <div className="flex flex-col min-h-screen bg-background">
      <Header />
      <main className="flex-1 flex items-center justify-center p-4">
        <div className="w-full max-w-2xl">
          {quizState === "idle" && (
            <Card className="text-center shadow-lg animate-in fade-in-0 zoom-in-95">
              <CardHeader>
                <CardTitle className="text-3xl font-headline">Ready to Begin?</CardTitle>
                <CardDescription>The quiz consists of {quizQuestions.length} questions and will help us understand your learning style.</CardDescription>
              </CardHeader>
              <CardContent>
                <Button size="lg" onClick={handleStart}>
                  <Sparkles className="mr-2 h-4 w-4" /> Let's Go!
                </Button>
              </CardContent>
            </Card>
          )}

          {quizState === "in_progress" && (
            <QuizQuestions
              question={quizQuestions[currentQuestion]}
              onAnswer={handleAnswer}
              progress={progress}
            />
          )}

          {quizState === "loading" && (
            <div className="flex flex-col items-center justify-center text-center gap-4">
              <Loader2 className="w-12 h-12 text-primary animate-spin" />
              <h2 className="text-2xl font-bold font-headline">Analyzing your results...</h2>
              <p className="text-muted-foreground">Finding your perfect coaching match!</p>
            </div>
          )}

          {quizState === "results" && recommendations && (
            <QuizResults 
              personalityType={recommendations.personalityType} 
              recommendations={recommendations.recommendations}
              onRestart={handleRestart}
            />
          )}
        </div>
      </main>
    </div>
  );
}
