"use server";

import { generateCoachingRationale } from "@/ai/flows/generate-coaching-rationale";
import { coachingProfiles, personalityTypes } from "./data";
import type { PersonalityType, Recommendation, Trait } from "./types";

export async function getCoachingRecommendations(
  answers: Record<number, Trait>
): Promise<{
  personalityType: PersonalityType;
  recommendations: Recommendation[];
}> {
  // 1. Calculate trait scores
  const traitScores = new Map<Trait, number>();
  for (const trait of Object.values(answers)) {
    traitScores.set(trait, (traitScores.get(trait) || 0) + 1);
  }

  // 2. Determine dominant trait and personality type
  const sortedTraits = [...traitScores.entries()].sort((a, b) => b[1] - a[1]);
  const dominantTrait = sortedTraits.length > 0 ? sortedTraits[0][0] : 'balanced-pace';
  
  const personalityType =
    personalityTypes.find((p) => p.trait === dominantTrait) ||
    personalityTypes.find(p => p.trait === 'balanced-pace')!; // Fallback

  // Get top 3 traits for matching
  const studentTraits = sortedTraits.slice(0, 3).map(t => t[0]);

  // 3. Match with coaching profiles
  const scoredCoachings = coachingProfiles.map((coaching) => {
    const score = coaching.tags.reduce((acc, tag) => {
      if (studentTraits.includes(tag)) {
        return acc + 1;
      }
      return acc;
    }, 0);
    return { ...coaching, score };
  });

  // 4. Sort and get top 3
  const top3Coaching = scoredCoachings.sort((a, b) => b.score - a.score).slice(0, 3);

  // 5. Generate AI rationale for each
  const recommendations: Recommendation[] = await Promise.all(
    top3Coaching.map(async (coaching) => {
      const rationaleResult = await generateCoachingRationale({
        studentTraits,
        coachingTags: coaching.tags,
      });

      return {
        ...coaching,
        rationale: rationaleResult.rationale,
      };
    })
  );

  return { personalityType, recommendations };
}
